import Vue from 'vue'
import { 
    Button,
    Form,
    FormItem,
    Input,
    Container,
    Header,
    Aside,
    Main ,
    Menu,
    Submenu,
    MenuItem,
    breadcrumb,
    breadcrumbItem,
    card,
    Row,
    Col,
    table,
    tableColumn,
    Switch,
    Tooltip,
    Pagination,
    Dialog,
    MessageBox 
} from 'element-ui'
//导入Message弹框提示
import { Message } from 'element-ui'

Vue.use(Button)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Input)
Vue.use(Container)
Vue.use(Header)
Vue.use(Aside)
Vue.use(Main)
Vue.use(Menu)
Vue.use(Submenu)
Vue.use(MenuItem)
Vue.use(breadcrumb)
Vue.use(breadcrumbItem)
Vue.use(card)
Vue.use(Row)
Vue.use(Col)
Vue.use(table)
Vue.use(tableColumn)
Vue.use(Switch)
Vue.use(Tooltip)
Vue.use(Pagination)
Vue.use(Dialog)
// 弹框组件挂载到vue的原型对象上 这样没个组件都可以通过this访问$Message
// 只要访问到$Message就可以得到弹框提示
Vue.prototype.$Message=Message
Vue.prototype.$confirm=MessageBox.confirm