import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Home from '../views/Home.vue'
import WelWelcome from '../views/Welcome.vue'
import Yhgl from '../views/Yhgl.vue'

Vue.use(VueRouter)


const router = new VueRouter({
  routes:[
      {
        path: '/',
        redirect:'/Login'
      },
      {
        path: '/Login',
        component: Login
      },
      {
        path: '/Home',
        component: Home,
        // 重定向
        redirect:'/WelWelcome',
        children:[
          {
            path: '/WelWelcome',
            component: WelWelcome,
          },
          {
            path: '/users',
            component: Yhgl
          },
        ]
      },

    
  ]
})

// 挂载路由导航守卫
router.beforeEach((to,from,next)=>{
  //to 将要to访问的路径
  // from 代表从哪个路径跳转而来
  // next是一个函数 表示放行

  if(to.path === '/Login') return next() // 判断访问的是不是登录页
  //获取token
  const tokenStr = window.sessionStorage.getItem('token')
  // 如果非token 那就证明token字符串不存在 不存在就强制返回login
  if(!tokenStr) return next ('/Login') 
  // 存在就直接放行
  next()
})
export default router
