<template>
  <div class="app">
    <el-container class="top">
        <!-- 头部 -->
        <el-header>
            <h1>后台管理系统</h1>
            <el-button type="info" @click="tc" class="tc">退出</el-button>
        </el-header>
        <el-container>
         <!-- 侧边栏 -->
        <el-aside :width="isColl? '64px':'200px'">
            <div class="toggle" @click="topggle">
                |||
            </div>
            <el-menu background-color="#333744" :default-active='activePath' router text-color="#fff" active-text-color="#409bff" :collapse-transition='false' :collapse='isColl' :unique-opened='true'>
                <!-- 一级菜单 -->
                <el-submenu :index="item.id+''" v-for="item in menulist" :key="item.id">
                    <!-- 一级菜单内容区 -->
                    <template slot="title">
                        <!-- 图标 -->
                    <i :class="icnosObj[item.id]"></i>
                    <!-- 文本 -->
                    <span>{{item.authName}}</span>
                    </template>
                <!-- 一级菜单内容区结束-->
                    <!-- 二级菜单 -->
                    <el-menu-item @click="state('/'+subItem.path)" :index="'/'+subItem.path" v-for="subItem in item.children" :key="subItem.id">
                        <template slot="title">
                        <!-- 图标 -->
                        <i class="el-icon-menu"></i>
                        <!-- 文本 -->
                        <span>{{subItem.authName}}</span>
                        </template>
                    </el-menu-item>
                    <!-- 二级菜单结束 -->
                </el-submenu>
            </el-menu>
        </el-aside>
        <!-- 右内容 -->
        <el-main>
            <router-view></router-view>
        </el-main>
        </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
name:"Home",
data() {
return {
    menulist:[],
    icnosObj:{
        '125':'iconfont icon-users',
        '103':'iconfont icon-tijikongjian',
        '101':'iconfont icon-shangpin',
        '102':'iconfont icon-danju',
        '145':'iconfont icon-baobiao',
    },
    // 是否折叠
    isColl:false,
    // 被激活的链接地址
    activePath:'activePath'
}
},
methods:{
    tc(){
        window.sessionStorage.clear()
        this.$router.push('../views/Login.vue')
    },
    // 获取所有菜单
    async getMenuList(){
        const {data:res} = await this.$http.get('menus')
        // return 后是失败   没有return就是成功
        if(res.meta.status!==200) return this.$message.error(res.meta.msg)
        //成功后赋值
        this.menulist = res.data
    },
    // 点击按钮切换
    topggle(){
        this.isColl=!this.isColl
    },
    // 保存连接的激活状态
    state(activePath){
        window.sessionStorage.setItem('activePath',activePath)
        this.activePath=activePath
    },

},
//生命周期 - 创建完成（访问当前this实例）
created() {
    this.getMenuList()
    this.activePath=window.sessionStorage.getItem('activePath')
    
},
//生命周期 - 挂载完成（访问DOM元素）
mounted() {

}
}
</script>
<style lang="less" scoped>
.top{
    height: 800px;
}
// 头部
.el-header{
    background: #373d41;
    display: flex;
    justify-content: space-between ;
    .tc{
        height: 70%;
        margin-top: 9px;
    }
}
// 侧边栏
.el-aside{
    background: #333744;
    .el-menu{
        border-right:none ;
    }
}
// 右内容部分
.el-main{
    background: #eaedf1;
}
.iconfont{
    margin-right: 10px;
}
.toggle{
    background: #4a5064;
    text-align: center;
    line-height: 24px;
    color: fff;
    font-size: 14px;
    letter-spacing: 0.2px;
    cursor: pointer;
}
</style>