<template>
  <div  class="loing_conta">
    <div class="login_box">
      <div class="box_img">
        <!-- 头像 -->
        <img src="../assets/logo.png" alt="">
      </div>
      <!-- 登陆 -->
      <el-form ref='loginRef' class="login_from" :model="login_from"  :rules="loginRules" label-width="0px">
          <!-- 用户名 -->
        <el-form-item prop="username" >
          <el-input v-model="login_from.username " prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
          <!-- 密码 -->
        <el-form-item prop="password">
          <el-input type='password' v-model="login_from.password " prefix-icon="iconfont icon-3702mima"></el-input>
        </el-form-item>
        <!-- 按钮 -->
        <el-form-item class="btns">
          <el-button type="primary" @click="logindl">登陆</el-button>
          <el-button type="info" @click="resetlogin">重置</el-button>
        </el-form-item>
      </el-form>  
    </div>
  </div>
</template>

<script>
export default {
name:"Login",
data() {
return {
  //登陆表单数据绑定对象
  login_from:{
    username:'',
    password:''
  },
  // 表单的验证
  loginRules:{
    // 验证用户名是否合法
    username:[
      { required: true, message: '请输入登录名称', trigger: 'blur' },
      { min: 3, max: 11, message: '长度在3到10个字符', trigger: 'blur'}
    ],
    // 验证密码是否合法
    password:[
        { required: true, message: '请输入登陆密码', trigger: 'blur' },
      { min: 6, max: 15, message: '长度在3到15个字符', trigger: 'blur'}
    ]
  }
} 
},
methods:{
  // 点击重置 重置表单
  resetlogin(){
    // 先给表单添加一个ref引用 他的这个值就是表单的实例对象
    // 可以this访问到$refs在点出来我们表单的引用对象 然后直接调用resetFields()方法就可以实现重置的这个方法
    this.$refs.loginRef.resetFields();
  },
  // 登录
  logindl(){
    // 获取到表单的引用对象
    // 通过应用对象调用validate函数进行表单的验证
    // 在validate他接收一个回调函数中的型参数
    // 这个型参是一个布尔值 然后判断这个布尔值 就可以知道这个表单是成功还是失败
    this.$refs.loginRef.validate(async valid=>{
      //首先判断valid是否为true 如果true就发起请求 如果不等于true直接return阻止这次请求
      // 如果验证通过配置一下路由请求跟路径 然后就可以通过this发起请求
      if(!valid) return;
      // post返回值是一个promise 为了简化promise操作 我们可以用async和await来进行优化
      // 同时要注意await只能用在async修饰的方法中
      const {data:res} = await this.$http.post('login',this.login_from)
      // console.log(res)
      if(res.meta.status!==200)
      //通过this调用$Message这个方法提供一个提示提示信息
      return this.$Message.error("登录失败");
      this.$Message.success('登录成功');
      // 登录成功后的token,保存到客户端中的sessionStorage中
      // 项目中除了登录之外的其他AIP接口 必须在登录之后才能访问
      // token只应在当前网站打开生效 所以讲token保存在sessionStorage
      window.sessionStorage.setItem("token",res.data.token); //这样就把token保存在sessionStorage中
      // 通过编程式导航跳转后台主页 
      this.$router.push('./Home');
    })
  }
},
//生命周期 - 创建完成（访问当前this实例）
created() {

},
//生命周期 - 挂载完成（访问DOM元素）
mounted() {

}
}
</script>
<style lang="less" scoped>
.loing_conta{
  background: #2b4b6b;
  height: 100%;
}
.login_box{
  width: 450px;
  height: 300px;
  background: rgb(98, 133, 133);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  .box_img{
    width: 110px;
    height: 110px;
    border: 1px solid rgb(124, 148, 131);
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px pink;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-30%);
    background: rgb(135, 173, 146);
    img{
      width: 100%;
      border-radius: 50%;
      background: rgb(124, 148, 131);
    }
  }
  .login_from{
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
    .btns{
      display: flex;
      justify-content: space-around;
    }
  }
}

</style>