<template>
  <div id="">   
      <!-- 导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片 -->
    <el-card class="box-card">
        <el-row :gutter='20'>
            <el-col :span="8">
                <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getUserList">
                    <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
                </el-input>
            </el-col>
            <el-col :span="4">
                <el-button type="primary" @click="addDialogVisible=true">添加用户</el-button>
            </el-col>
        </el-row>

        <!-- 用户列表 -->
        <el-table :data="userlist" border stripe>
            <el-table-column type='index'></el-table-column>
            <el-table-column label="姓名" prop="username"></el-table-column>
            <el-table-column label="邮箱" prop="email"></el-table-column>
            <el-table-column label="电话" prop="mobile"></el-table-column>
            <el-table-column label="角色" prop="role_name"></el-table-column>
            <el-table-column label="状态" >
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.mg_state" @change="userChange(scope.row)"></el-switch>
                </template>
            </el-table-column>
            <el-table-column label="操作" width='180px'>
                <template slot-scope="scope">
                    <!-- 修改 -->
                    <el-button size='mini' type="primary" icon="el-icon-edit" @click="showEditxg(scope.row.id)"></el-button>
                    <!-- 删除 -->
                    <el-button size='mini' type="danger" icon="el-icon-delete" @click="scById(scope.row.id)"></el-button>
                    <!-- 分配 -->
                    <el-tooltip  effect="dark" content="分配角色" placement="top" :enterable='false'>
                        <el-button size='mini' type="warning" icon="el-icon-setting"></el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页区域 -->
        <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
        </el-pagination>
        <!-- 分页区域 结束-->
        <!--  添加用户对话框 -->
        <el-dialog title="添加用户" :visible.sync="addDialogVisible" width="50%" @close="addClose" >
        <!-- 内容区 -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
            <el-form-item label="用户名" prop="username">
                <el-input v-model="addForm.username"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
                <el-input v-model="addForm.password"></el-input>
            </el-form-item>
            <el-form-item label="邮箱" prop="email">
                <el-input v-model="addForm.email"></el-input>
            </el-form-item>
            <el-form-item label="手机号" prop="mobile">
                <el-input v-model="addForm.mobile"></el-input>
            </el-form-item>
        </el-form>    
        <!-- 对话框按钮区域 -->
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addUser">确 定</el-button>
        </span>
        </el-dialog>
        <!--  添加用户对话框结束-->
        <!-- 修改用户对话框 -->
        <el-dialog title="修改用户" :visible.sync="xgDialogVisible" width="50%" @close='xgClosse' >
            <el-form :model="xgForm" :rules="xgFormRules" ref="xgFormRef" label-width="70px" >
                <el-form-item label="用户名">
                    <el-input v-model="xgForm.username" disabled></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input v-model="xgForm.email" ></el-input>
                </el-form-item>
                <el-form-item label="手机号" prop="mobile">
                    <el-input v-model="xgForm.mobile" ></el-input>
                </el-form-item>
            </el-form>    
            <span slot="footer" class="dialog-footer">
                <el-button @click="xgDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="xgUserInfo">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 修改用户对话框结束 -->
    </el-card>
    <!-- 卡片结束 -->
  </div>
</template>

<script>
export default {
name:"",
data() {
    // 邮箱验证
    var checkEmail=(rule,value,cb)=>{
        //验证邮箱正则表达式
        const regEmail =/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
        if(regEmail.test(value)){
            // 合法
            return cb()
        }
        // 如果不合法给个弹框提示
        cb(new Error('请输入合法邮箱'))
    }
    // 手机验证
    var checkMobile=(rule,value,cb)=>{
        //验证手机正则
        const regMobile = /^(0|[3-9]|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
        if(regMobile.test(value)){
            return cb()
        }
        cb(new Error('请输入合法手机号'))
    }
    return {
        // 获取用户列表参数对象
        queryInfo:{
            query:'',
            //当前页数
            pagenum:1,
            //显示当前页数
            pagesize:5,
        },
        // 所有的用户列表
        userlist:[],
        // 钟数据行数
        total:0,
        addDialogVisible:false,
        //添加用户的数据
        addForm:{
            username:'',
            password:'',
            email:'',
            mobile:''
        },
        // 添加表单验证规则对象
        addFormRules:{
            uesrname:[
                {
                    required:true,
                    message:'请输入用户名',
                    trigger:'blur'
                },
                {
                    min:3,nax:10,message:'用户名长度3-10字符',
                    trigger:'blur'

                }
            ],
            password:[
                {
                    required:true,
                    message:'请输入密码',
                    trigger:'blur'
                },
                {
                    min:6,nax:16,message:'用户名长度6~16字符',
                    trigger:'blur'

                }
            ],
            email:[
                {
                    required:true,
                    message:'请输入邮箱',
                    trigger:'blur'
                },
                {
                    validator:checkEmail,
                    trigger:'blur',
                }
            ],
            mobile:[
                {
                    required:true,
                    message:'请输入手机号',
                    trigger:'blur',
                },
                {
                    validator:checkMobile,
                    trigger:'blur'
                }
            ]
        },
        // 控制修改对话框的显示隐藏
        xgDialogVisible:false,
        //查询到的用户信息对象
        xgForm:{},
        //修改表单验证规则
        xgFormRules:{
            email:[
                {
                    required:true,
                    message:'请输入邮箱',
                    trigger:'blur'
                },
                {
                    validator:checkEmail,
                    trigger:'blur',
                }
            ],
            mobile:[
                {
                    required:true,
                    message:'请输入手机号',
                    trigger:'blur',
                },
                {
                    validator:checkMobile,
                    trigger:'blur'
                }
            ]
        }
    }
},
//生命周期 - 创建完成（访问当前this实例）
created() {
    this.getUserList()
},
methods:{
    async getUserList(){
    const  {data:res} = await this.$http.get('users',{
            params:this.queryInfo
        })
        //如果res不等于200 就是获取失败
        if(res.meta.status !== 200){
            return this.$message.error('获取用户列表失败')
        } 
        // 成功就赋值
        this.userlist = res.data.users
        this.total= res.data.total
        // console.log(res)
    },
    // 监听pagesize改变的事件
    handleSizeChange(newSize){
        // console.log(newSize)
        this.queryInfo.pagesize=newSize
        //newSize发生改变 接下来就重新发起请求来获取数据
        this.getUserList()
    },
    //监听页码值改变事件
    handleCurrentChange(newPage){
        // console.log(newPage)
        this.queryInfo.pagenum = newPage
        //newPage发生改变 接下来就重新发起请求来获取数据
        this.getUserList()
    },
    //监听switch 开关状态改变
    async userChange(userinfo){
        // console.log(userinfo)
        const {data:res} =await  this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
        // console.log(res)
        // 如果不等于200 就是处理失败 终止操作 提示用户更新失败
        if(res.meta.status !==200){
            //并且将用户状态取反  保证状态没有任何的变比
            userinfo.mg_state =!userinfo.mg_state
            return this.$Message.error("更新用户状态失败");
        };
        // 没有return就说明成功 给用户提示更新状态成功
        this.$Message.success('更新用户状态成功');
    },
    //监听添加用户对话框的关闭事件 关闭后要重置
    addClose(){
        this.$refs.addFormRef.resetFields()
    },
    //点击添加新用户
    addUser(){
        this.$refs.addFormRef.validate(async valid=>{
            // console.log(valid)
            // 如果验证失败 则不走添加项
            if(!valid) return

            // 如果没有被return  就可以添加用户请求
            const {data:res} =await this.$http.post('users',this.addForm)
                // 如何不等201那就是失败
            if(res.meta.status!==201){
                // 给用户提示一条失败信息
                this.$Message.error('添加失败');
            }
                // 给用户提示一条成功信息
            this.$Message.success('添加用户成功!')
            // 隐藏添加用户对话框
            this.addDialogVisible=false
            // 重新获取列表数据
            this.getUserList()
        })
    },
    //展示编辑对话框
    async showEditxg(id){

        const {data:res}  =await this.$http.get('users/'+id)
        // 如果res.meta.status!==200 请求失败 终止操作 弹出查询信息失败
        if(res.meta.status!==200){
            return this.$Message.error('查询信息失败');
        }
        // this.$Message.success('查询信息成功!')
        this.xgForm=res.data
        this.xgDialogVisible =true
    },
    // 监听修改对话框的关闭事件
    xgClosse(){
        //重置对话框
        this.$refs.xgFormRef.resetFields()
    },
    //修改用户信息并且提交
    xgUserInfo(){
        this.$refs.xgFormRef.validate(async valid=>{
            // console.log(valid)
            // 如果!valid就直接return
            if(!valid)return
            //如果没有return就是通过 可以发起修改用户信息请求
            const {data:res} = await this.$http.put('users/'+this.xgForm.id,
            {email:this.xgForm.email,mobile:this.xgForm.mobile})
            //如果res.meta.atatus不等200 然后返回一个提示
            if(res.meta.status !==200){
                return this.$Message.error('更新用户信息失败');
            }
            // 关闭对话框
            this.xgDialogVisible=false
            //刷新数据列表
            this.getUserList()
            //提示修改成功
            this.$Message.success('更新用户信息成功')
        }) 
    },
    // 删除响应的ID
    async scById(id){
        //弹框提示是否删除
        const scResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>err)
        // console.log(scResult)
        //如果用户确认删除 则返回字符串confirm
        //如果取消 则返回 字符串cancel

        // 判断scResult不是字符串confirm'  说明用户点击取消删除
        if(scResult!=='confirm'){
            return this.$Message.error('以取消删除');
        }
        const {data:res} = await this.$http.delete('users/'+id)
        //如果res.meta.status不等200 呢就是删除失败了
        if(res.meta.status !==200){
            // 删除失败后给用户一个提示
            return this.$Message.error('删除用户失败');
        }
        // 删除成功也给用户一个提示
        this.$Message.success('删除用户成功')
        // 删除成功后刷新页面数据
        this.getUserList();
    }   

},
//生命周期 - 挂载完成（访问DOM元素）
mounted() {

}
}
</script>
<style scoped>
/* @import url(); 引入css类 */

</style>